import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import { makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import MuiDialogTitle from '@material-ui/core/DialogTitle';
import MuiDialogContent from '@material-ui/core/DialogContent';
import MuiDialogActions from '@material-ui/core/DialogActions';
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';
import Typography from '@material-ui/core/Typography';
import { TextField } from '@material-ui/core';
import Table from './table'
import { GroupeAddPar } from "./../dll/ajax";

const styles = (theme) => ({
  root: {
    margin: 0,
    padding: theme.spacing(2),
  },
  closeButton: {
    position: 'absolute',
    right: theme.spacing(1),
    top: theme.spacing(1),
    color: theme.palette.grey[500],
  },
 
});

const useStyles = makeStyles({
    paperWidthSm: {
      manWidth: 900,
    },
  });
 
const DialogTitle = withStyles(styles)((props) => {
  const { children, classes, onClose, ...other } = props;
  return (
    <MuiDialogTitle disableTypography className={classes.root} {...other}>
      <Typography variant="h6">{children}</Typography>
      {onClose ? (
        <IconButton aria-label="close" className={classes.closeButton} onClick={onClose}>
          <CloseIcon />
        </IconButton>
      ) : null}
    </MuiDialogTitle>
  );
});

const DialogContent = withStyles((theme) => ({
  root: {
    padding: theme.spacing(2),
  },
}))(MuiDialogContent);

const DialogActions = withStyles((theme) => ({
  root: {
    margin: 0,
    padding: theme.spacing(1),
  },
}))(MuiDialogActions);
 
export default function CustomizedDialogs(props) {
  const [open, setOpen] = React.useState(false);
 
  const handleClickOpen = () => {
    setOpen(true);
  };
  const handleClose = () => {
   
    setOpen(false);
  };
  function sendToURL(){
    let dayWeek =  props.dayWeek;
    let lesson =
       {lessons:props.lessons[dayWeek-1].lessons  }
    
    GroupeAddPar(lesson);
  }
  const classes = useStyles();//onClick={handleClose}
  return (
    <form >
      <Button variant="outlined"  color="primary" onClick={handleClickOpen}>
        Редактировать
      </Button>
      <Dialog onClose={handleClose}  className={classes.root} aria-labelledby="customized-dialog-title" open={open}>
        <DialogTitle id="customized-dialog-title" onClose={handleClose}>
          Редактировать расписание
        </DialogTitle>
        <DialogContent dividers>
            <Table lessons={props}/> 
        </DialogContent>
        <DialogActions>
          <Button autoFocus onClick={sendToURL}  color="primary">
            Сохранить
          </Button>
          

        </DialogActions>
      </Dialog>
    </form>
  );
}