import React, { Component } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import { TextField } from '@material-ui/core';
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';

class table extends Component {
  state = {
    lesson: [],

  }
  constructor(props) {
    super(props);
    console.log("s22", props);


    this.Change = this.Change.bind(this);
  }


  Change(e, data) {

    
    let lesson = this.props.lessons.lessons;
    let groupId = this.props.lessons.groupId;
    let dayWeek = this.props.lessons.dayWeek;
     
   

      if ( e.name === "teachers") {
        
        if (e.name === "teachers") {

         /* lesson[e.k]['teachers'].map((item) => {
            console.log(item);
            if (Number(item.num) == Number(e.num)) {
              lesson[e.k]['teachers'] = [{ id: data.target.value, num: e.num }]
            } else if (lesson[e.k]['teachers'].length == 1) {
              lesson[e.k]['teachers'].push({ id: data.target.value, num: e.num })
            }
          });*/
          let lessons = lesson[dayWeek-1]['lessons'];
          console.log("d",lesson)
          if(lessons[e.k] == undefined){
            lessons[e.k] = {'teachers': [{
              id:data.target.value,
              num:e.num
            }]
          }
           
          }else{
            if(lessons[e.k]['teachers'][e.th]==undefined){
              lessons[e.k]['teachers'][e.th]= {
                id:data.target.value,
                    num:e.num
                }
            }else{
              lessons[e.k]['teachers'][e.th] = {
                    id:data.target.value,
                    num:e.num
                    }
            }
             

          }





        } else {
          let lessons = lesson[dayWeek-1]['lessons'][e.k];
          lessons [e.name][e.n] = { name: data.target.value };
        }

      } else {
        const key = e.k;
        const name = e.name
        const upLesson = [];
        
          if( lesson[dayWeek-1]['lessons'][key]  == undefined){

            let day =  {
              "pairNumber": 0,
              "day": 0,
              "discipline": "",
              "auditorium": "",
              "weekType": "",
              "group": {
                "id": lesson[dayWeek-1].group_id,
                "name": "",
      
              },
              "teachers": [
                {
                  "id": 0,
                  "name": "",
      
                }
              ],
      
            };
            day.name = data.target.value;
            day.day =dayWeek;
            lesson[dayWeek-1]['lessons'].push(key);
            lesson[dayWeek-1]['lessons'][key]=[];
            console.log(day);
            lesson[dayWeek-1]['lessons'][key]=day;
            
            
          }else{
            lesson[dayWeek-1]['lessons'][key][name]=data.target.value;
             lesson[dayWeek-1]['lessons'][key]['day']=dayWeek;
          }
             
             
          
      }



 /*try {
      //lesson[e.key][e.name]= data.target.value;
    } catch (error) {
     
      lesson[e.k] =
      {
        "pairNumber": 0,
        "day": 0,
        "discipline": "",
        "auditorium": "",
        "weekType": "",
        "group": {
          "id": 0,
          "name": "",

        },
        "teachers": [
          {
            "id": 0,
            "name": "",

          }
        ],

      }

    }*/
     console.log("new lesson",lesson);

    // this.props.lessons.lessons = lesson;

  }

  render() {
    function createData(name, calories, fat, carbs, protein, teachers) {
      return { name, calories, fat, carbs, protein, teachers };
    }




    let props = this.props;
    const classes = makeStyles({
      table: {
        minWidth: 650,
      },
    });
    function lessons(props) {
      let item = [];
      console.log('props',props);
      props.lessons.lessons[props.lessons.dayWeek-1].lessons.map((body) => {
        console.log('body',body);
        if (props.lessons.week == body.weekType || body.weekType == "NONE") {
          item.push(createData(body.discipline, body.pairNumber, body.weekType, "", body.auditorium, body.teachers))
        }
      })
      if (item.length < 6) {
        for (let i = item.length; i < 6; i++) {
          item.push(createData("", "", "", "", ""))
        }
      }
      return item;
    };
    const rows = lessons(props);


    const weekType = (row, key) => {

      const classes = {
        root: {
          with: 200,
        }
      }

      if (row.fat == "NONE") {
        return (<select
          className={classes.root}
          onChange={this.Change.bind('name', { name: 'weekType', k: key, n: 0 })}
        >
          <option value='NONE'>верхняя-нижняя</option>
          <option value='UP'>верхняя</option>
          <option value='DOWN'>нижняя</option>
        </select>
        );
      }
      if (row.fat == "UP") {
        return (<select
          className={classes.root}

          onChange={this.Change.bind('name', { name: 'weekType', k: key, n: 0 })}
        >
          <option value='UP'>верхняя</option>
          <option value='NONE'>верхняя-нижняя</option>
          <option value='DOWN'>нижняя</option>
        </select>
        );
      }
      if (row.fat == "DOWN") {
        return (<select
          className={classes.root}

          onChange={this.Change.bind('name', { name: 'weekType', key: key, n: 0 })}
        >
          <option value='DOWN'>нижняя</option>
          <option value='UP'>верхняя</option>
          <option value='NONE'>верхняя-нижняя</option>

        </select>
        );
      }
      if (row.fat == "") {
        return (<select
          className={classes.root}

          onChange={this.Change.bind('name', { name: 'weekType', k: key, n: 0 })}
        >
          <option value='DOWN'>нижняя</option>
          <option value='UP'>верхняя</option>
          <option value='NONE'>верхняя-нижняя</option>

        </select>
        );
      }
    }
    /**
     * 
     * @param {массив пары} item 
     * @param {номер пары в массиве} key 
     * @param {количество} n 
     * @returns 
     */
    const teachers = (item, key, n) => {
      console.log(item, key, n);
      const classes = {
        root: {
          with: 200,
        }
      }
      let TeachersSelect = this.props.lessons.Teachers;

      if (this.props.lessons.lessons[key].lessons[key] !== undefined) {

        let teacgers = this.props.lessons.lessons[key].lessons[key].teachers;
        console.log("tch",this.props.lessons.lessons[key].lessons[key].teachers);
        if (teacgers !== undefined) {
            try {
              return (
                <select
                  className={classes.root}
                  onChange={this.Change.bind('name', { name: 'teachers', k: key, num: item.teachers[n].num, th: n })}
                  key={key}
                >
                  <option value={teacgers[n].id}>{teacgers[n].name}</option>
                  {TeachersSelect.map((item, rey) => (
                    <option key={rey} value={item.id}>{item.name}</option>

                  ))}
                </select>
              )
            } catch (error) {
              return (<select
                className={classes.root}
                onChange={this.Change.bind('name', { name: 'teachers', k: key, num: n, th: n })}
                key={key}
              >
                <option value="">Укажите преподавателя</option>
                {TeachersSelect.map((item, rey) => (
                  <option key={rey} value={item.id}>{item.name}</option>
    
                ))}
              </select>)
            }
          }
          else {
          return (<select
            className={classes.root}
            onChange={this.Change.bind('name', { name: 'teachers', k: key, num: n, th: n })}
            key={key}
          >
            <option value="">Укажите преподавателя</option>
            {TeachersSelect.map((item, rey) => (
              <option key={rey} value={item.id}>{item.name}</option>

            ))}
          </select>)
        }





      }
      else {
        return (
          <select
            className={classes.root}
            onChange={this.Change.bind('name', { name: 'teachers', k: key, n: 1 })}
            key={key}
          >
            <option value="">Укажите преподавателя</option>
            {TeachersSelect.map((item, rey) => (
              <option key={rey} value={item.id}>{item.name}</option>

            ))}
          </select>
        )

      }




    }
    return (

      <TableContainer component={Paper}>
        <Table className={classes.table} aria-label="caption table">

          <TableHead>
            <TableRow>
              <TableCell>Название предмета</TableCell>
              <TableCell align="right">№ пары</TableCell>
              <TableCell align="right">Неделя</TableCell>
              <TableCell align="right">Преподаватель</TableCell>
              <TableCell align="right">Аудитория</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {rows.map((row, key) => (
              <TableRow  >
                <TableCell component="th" scope="row">
                  <TextField label="Предмет" id="standard-basic" onChange={this.Change.bind('name', { name: 'discipline', k: key, n: 0 })} defaultValue={row.name} />
                </TableCell>
                <TableCell align="right"><TextField label="№ пары" onChange={this.Change.bind('name', { name: 'pairNumber', k: key, n: 0 })} defaultValue={row.calories} /></TableCell>
                <TableCell align="right">
                  {weekType(row, key)}
                </TableCell>
                <TableCell align="right">
                  {teachers(row, key, 0)}
                  {teachers(row, key, 1)}

                </TableCell>
                <TableCell align="right"><TextField label="Аудитория" onChange={this.Change.bind('name', { name: 'auditorium', k: key, n: 0 })} defaultValue={row.protein} /></TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>

    );

  }
}
export default table;
