import React, { Component } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import { GrouGroupeLessonpeURL } from "./../dll/ajax";
import { TeachersURL } from "./../dll/ajax";
import Progress from "./../../Progress";
import { TextureRounded } from '@material-ui/icons';
import Dialog from './dialog';

class Cart extends Component {

  state = {
    open: true,
    Groupe: null,
    Progressbar: null,
    week:'UP',
    Teachers:null
  };

  componentWillReceiveProps(nextProps) {
    let Progressbar = null;
    this.setState({Progressbar});
    GrouGroupeLessonpeURL(this.props.state.cart.data).then((item) => {
      console.log("componentWillReceiveProps",item);
      const countNoDay = [];
      const countYesDay = [];
      for(let i=1; i<6; i++){
        if(item.data[i-1]==undefined){
          console.log("no ",item.data[i]);
            countNoDay.push(i);

        }else{
          countYesDay.push(i-1);
         /// countYesDay.push(item.data[i-1].lessons[0].day);
        }
      }
      for(let i=0; i<countNoDay.length; i++){
        item.data.push({
          "group_id": item.data[countYesDay[0]].group_id,"lessons":[{
            "pairNumber": 0,
            "day": countYesDay[0],
            "discipline": "",
            "auditorium": "",
            "weekType": "NONE",
            "group": {
              "id": item.data[countYesDay[0]].group_id,
              "name": "",
    
            },
            "teachers": [],
    
          }]});
      }
      
     // item.data.sort(sortDay);
       
      this.setState({Groupe:item.data });
      console.log(this.state.Groupe);
       
      this.setState({Progressbar:false});
      let week = this.props.state.week;
      this.setState({week});

    
     
    });
    TeachersURL().then((item) => { 
      let Teachers = item.data._embedded.teacherModelList;
      this.setState({Teachers});
      console.log(this.state.Teachers);
    });
  }
  componentDidMount() {
    GrouGroupeLessonpeURL(this.props.state.cart.data).then((item) => {
      console.log(item);
      var p = 1;
      function sortDay(a,b){
        console.log("a",a.lessons[0].day);
        console.log("b",b.lessons[0].day);
         p++;
         console.log('p',p);
         if(a.lessons[0].day==p) return 0
          
      if(a.lessons[0].day==0) {  return +1;}
      }
      
      const countNoDay = [];
      const countYesDay = [];
      for(let i=1; i<6; i++){
         
        if(item.data[i-1]==undefined){
           
            countNoDay.push(i);

        }else{
          //countYesDay.push(item.data[i-1].lessons[0].day);
          countYesDay.push(i-1);
        }
      }
      for(let i=0; i<countNoDay.length; i++){
        item.data.push({
          "group_id": item.data[countYesDay[0]].group_id,"lessons":[{
            "pairNumber": 0,
            "day": countYesDay[0],
            "discipline": "",
            "auditorium": "",
            "weekType": "NONE",
            "group": {
              "id": item.data[countYesDay[0]].group_id,
              "name": "",
    
            },
            "teachers": [],
    
          }]});
      }
      console.log("no day",countNoDay);
      console.log("yes day",countYesDay);
      
       

      //let data = item.data.sort(sortDay);
     // console.log(data);
      this.setState({Groupe:item.data });

      

     
       
      this.setState({Progressbar:false});
      let week = this.props.state.week;
      this.setState({week});
    });
    TeachersURL().then((item) => { 
      let Teachers = item.data._embedded.teacherModelList;
      this.setState({Teachers});
   
    });
  }
  render() {
    const classes = {
      root: {
        minWidth: 275,
      },
      bullet: {
        display: 'inline-block',
        margin: '0 2px',
        transform: 'scale(0.8)',
      },
      title: {
        fontSize: '14',
      },
      pos: {
        marginBottom: 12,
      },
    };
    
    function lessons(item,week) {
     return (
        <div>
          {item.map((body, key) => {
            if(body.weekType == week || body.weekType=="NONE"){
              return(<div key={key}>
                {body.pairNumber} | {body.discipline} | {body.teachers.map((item,key)=>{ return(<span key={key}>{item.name}|</span>) })}  {body.auditorium}
              </div>)
            }
          })}
        </div>
      );
    }
    
    function dayWeek(day) {
      switch (day) {
        case 1:
          return "Понедельник";
        case 2:
          return "Вторник";
        case 3:
          return "Среда";
        case 4:
          return "Четверг";
        case 5:
          return "Пятница";
        case 6:
          return "Суббота";
        case 7:
          return "Воскресенье";
      }
    }
   
    if (this.state.Progressbar === null) {
      return(<Progress />)
    } else {
       
      return (
        <div>

          { this.state.Groupe.map((body, key) => (
            
            <Card className={classes.root} key={key} variant="outlined">
              <CardContent>
                <Typography className={classes.title}  gutterBottom>
                   {dayWeek(body.day)}
                </Typography>
                <Typography variant="body2" component="p">
                  {lessons(body.lessons,this.state.week)}
                </Typography>
              </CardContent >
              <CardActions>
                <Dialog lessons={this.state.Groupe} week={this.state.week} groupId={body.group_id} Teachers={this.state.Teachers} dayWeek={key+1} />
              </CardActions>
            </Card>
          ))
          }
        </div>

      );
    }

  }
}



export default Cart;