import React, { Component } from 'react';
import { withStyles } from '@material-ui/core/styles';
import { makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import MuiDialogTitle from '@material-ui/core/DialogTitle';
import MuiDialogContent from '@material-ui/core/DialogContent';
import MuiDialogActions from '@material-ui/core/DialogActions';
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';
import Typography from '@material-ui/core/Typography';
import { TextField } from '@material-ui/core';
import Table from './table'
import { GroupeAddPar } from "./../dll/ajax";


class NewGrope extends Component {
    state  = {
            setOpen:true,
            newState:null
        }
    
    
    Change(e, data) {
       let newState = e.target.value
       this.setState({newState});

   }
    render() {
        
        const styles = (theme) => ({
            root: {
                margin: 0,
                padding: theme.spacing(2),
            },
            closeButton: {
                position: 'absolute',
                right: theme.spacing(1),
                top: theme.spacing(1),
                color: theme.palette.grey[500],
            },

        });

        

        const DialogTitle = withStyles(styles)((props) => {
            const { children, classes, onClose, ...other } = props;
            return (
                <MuiDialogTitle disableTypography className={classes.root} {...other}>
                    <Typography variant="h6">{children}</Typography>
                    {onClose ? (
                        <IconButton aria-label="close" className={classes.closeButton} onClick={onClose}>
                            <CloseIcon />
                        </IconButton>
                    ) : null}
                </MuiDialogTitle>
            );
        });

        const DialogContent = withStyles((theme) => ({
            root: {
                padding: theme.spacing(2),
            },
        }))(MuiDialogContent);
        function sendToURL() {
            let s = this.state.newGroupe;
            console.log(s);
            // GroupeAddPar("lesson");
        }
        const DialogActions = withStyles((theme) => ({
            root: {
                margin: 0,
                padding: theme.spacing(1),
            },
        }))(MuiDialogActions);
              
           


            const handleClose = () => {
                let setOpen = !this.state.setOpen
                this.setState({setOpen});
                 
            };

            const classes =   {
                manWidth: 900,
            } 
            return (

                <Dialog onClose={handleClose} className={classes.root} aria-labelledby="customized-dialog-title" open={this.state.setOpen}>
                    <DialogTitle id="customized-dialog-title" style={{marginLeft:'10px'}} onClose={handleClose}>
                        Создать группу
                    </DialogTitle>
                    <DialogContent dividers>
                        <TextField label="Название группы" onChange={this.Change} defaultValue={""} />
                    </DialogContent>
                    <DialogActions>
                        <Button autoFocus onClick={sendToURL} color="primary">
                            Сохранить
                    </Button>
                    </DialogActions>
                </Dialog>

            );
         
    }
}
export default NewGrope;