
import axios from "axios";

export async function GroupeURL(item, response) {
  try {

    let data = await axios.get('http://chtotib-bot-rest-service.herokuapp.com/api/groups/')
    return (data);

  } catch (error) {
    console.error(error);
  }
}
export async function NewGroupeURL(groupe) {
  try {

    let data = await axios.post(' http://chtotib-bot-rest-service.herokuapp.com/api/groups',groupe)
    return (data);

  } catch (error) {
    console.error(error);
  }
}
