import React, { Component } from 'react';
import { makeStyles, Theme, createStyles } from '@material-ui/core/styles';
import ListSubheader from '@material-ui/core/ListSubheader';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import Collapse from '@material-ui/core/Collapse';
import DashboardIcon from '@material-ui/icons/Dashboard';
import ExpandLess from '@material-ui/icons/ExpandLess';
import ExpandMore from '@material-ui/icons/ExpandMore';
import StarBorder from '@material-ui/icons/StarBorder';
import { GroupeURL, NewGroupeURL,NewFileURL } from './dll/ajax';
import Scroll from './../Progress';
import { TextField } from '@material-ui/core';
import './../index.css'
import { Route, NavLink } from 'react-router-dom';
import { MicNone } from '@material-ui/icons';
import Dialog from './proupe/newGrope';
import Button from '@material-ui/core/Button';
import Snackbar from '@material-ui/core/Snackbar';
class MainListItems extends Component {

  constructor(props){
    super(props);
    this.state = {
      open: true,
      groupe: [],
      scroll: true,
      dialog: false,
      newState: null,
      file:null,
      openSnackbar:false,
      message:"запрос выполнен"
    };
    this.onFormSubmit = this.onFormSubmit.bind(this)
    this.onChange = this.onChange.bind(this)
     
  }
  
  Change(e) {
    let newState = e.target.value;
    this.setState({ newState });
  }
  componentDidMount() {

    GroupeURL().then((item) => {
      let groupe = item.data._embedded.groupModelList;
      this.setState({ groupe });
      this.setState({ scroll: false });

    });

  
  }

  onFormSubmit(e){
    e.preventDefault();
     
    
    NewFileURL(this.state.file).then((item) => {
      this.setState({openSnackbar:true});
      console.log(item);
      this.setState({message:item});
    }    
    )
  }
  onChange(e) {
    this.setState({file:e.target.files[0]})
  }
  render() {
    const handleClick = () => {
      let open = !this.state.open;
      this.setState({ open });
    };
    const newGroupe = () => {

      let openNew = !this.state.openNew;
      this.setState({ openNew });
    };
    const newFile = () => {

      let openFile = !this.state.openFile;
      this.setState({ openFile });
    };

    const handleClose = () => {
      let openSnackbar = false;
      this.setState({openSnackbar});
      
    };
    function sendToURL() {
      let name = this.state.newState;

      NewGroupeURL({ name: name });
      GroupeURL().then((item) => {
        let groupe = item.data._embedded.groupModelList;
        this.setState({ groupe });
        this.setState({ scroll: false });

      });

    }
    let styles = {
      overflow: 'auto',
      maxHeight: '600px'
    }
    if (this.state.scroll) {
      return (<Scroll />)
    } else {
      return (
        <div>

          <ListItem button onClick={newGroupe}>
            <ListItemIcon>
              <DashboardIcon />
            </ListItemIcon>
            <ListItemText primary="Создать группу" />
          </ListItem>

          <Collapse in={this.state.openNew} timeout="auto" unmountOnExit>
            <List component="div" disablePadding>
              <ListItemText>
                <p><TextField label="Название группы" style={{ marginLeft: '10px' }} onChange={this.Change.bind(this)} defaultValue={""} /></p>
                <p><Button autoFocus onClick={sendToURL.bind(this)} color="primary">
                  Создать
                    </Button></p>
              </ListItemText>
            </List>
          </Collapse>
          
          
          
          <ListItem button onClick={newFile}>
            <ListItemIcon>
              <DashboardIcon />
            </ListItemIcon>
            <ListItemText primary="Импорт документа" />
          </ListItem>
          <Collapse in={this.state.openFile} timeout="auto" unmountOnExit>
            <List component="div" disablePadding>
              <ListItemText>
                <form   onSubmit={this.onFormSubmit}>
                  <h5>Прекрепить документ</h5>
                  <input type="file" onChange={this.onChange} />
                  <br />
                  <br />
                  <Button
                    type="submit"
                    variant="contained"
                    color="primary"
                   
                  >
                    Загрузить работу
                  </Button>
                  <Snackbar
                    open={this.state.openSnackbar}
                    onClose={handleClose}
                    TransitionComponent='SlideTransition'
                    message={this.state.message}

                  />
                  <p>{this.state.data}</p>
                </form>
              </ListItemText>
            </List>
          </Collapse>

          <ListItem button onClick={handleClick}>
            <ListItemIcon>
              <DashboardIcon />
            </ListItemIcon>
            <ListItemText primary="Группы" />
          </ListItem>

          <Collapse in={this.state.open} timeout="auto" style={styles} unmountOnExit>
            <List component="div" disablePadding>
              {this.state.groupe.map((body, key) => (
                <NavLink to={{ pathname: "/gproupe/" + body.id, state: { data: body._links.lessons.href, id: body.id } }} className="link"  >
                  <ListItem button key={key}>
                    <ListItemIcon>
                      <StarBorder />
                    </ListItemIcon>
                    <ListItemText primary={body.name} />
                  </ListItem>
                </NavLink>
              ))}
            </List>
          </Collapse>


        </div>
      );
    }

  }
}



export default MainListItems;





